main {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.intro {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cont-avatar {
  width: 200px;
  height: 200px;
  background-color: #22C5A7;
  border-radius: 50%;
  overflow: hidden;
}

.intro > h1 {
  color: whitesmoke;
  font-size: 3em;
  text-shadow: 1px 3px 0 #00FF85;
  margin-bottom: 0;
}

.intro > h2 {
  color: #68FFB7;
}

.intro > a {
  text-decoration: none;
  color: #68FFB7;
  border: 2px solid #68FFB7;
  padding: 10px 15px;
  border-radius: 5px;
}

/* PROJETOS */

.projects {
  width: 60%;
  color: #68FFB7;
}

.project {
  background-color: rgb(41, 41, 41);
  border-radius: 5px;
  width: 15vw;
  height: 10vw;
}
